import gql from 'graphql-tag'

export const PAGEINFO_QUERY = gql`
    query pageInfo ($_id: ID!) {
        pageInfo (_id: $_id) {
            _id
            url
            memo
        }
    }
`

export const ALLPAGESINFO_QUERY = gql`
    query allPagesInfo ($domain: ID!) {
        allPagesInfo(domain: $domain) {
            _id
            name
            path
            domain
            groups
            components
            memo
        }
    }
`

export const PAGESINFO_QUERY = gql`
    query pagesInfo ($ids: [ID!]!) {
        pagesInfo(ids: $ids) {

            _id
            name

        }
    }

`

export const CREATEPAGE_MUTATION = gql`
    mutation createPage ($page: PageInput!) {
        createPage (page: $page) {
            _id
            name
            path
            domain
            groups
            components
            memo
        }
    }
`

export const MODIFYPAGE_MUTATION = gql`
    mutation modifyPage ($page: PageInput!) {
        modifyPage (page: $page) {
            _id
            name
            path
            domain
            groups
            components
            memo
        }
    }
`



